.home {
	background: linear-gradient(to bottom, $purple 0%, $celeste 40%, $purple 100%);

	header {
		position: fixed; }

	h1 {
		font-size: 100px;
		font-weight: 900; }

	h2 {
		font-size: 60px;
		font-weight: 800; }

	.hero {
		display: flex;
		justify-content: center;
		align-items: center;
		color: $white;
		// background-image: url(../src/home/Smoke_compressed.png)
		// background-size: cover
		// filter: contrast(2)
		height: 100vh;
		min-height: rem(360px);
		margin-bottom: 20px;
		h1 {
			font-weight: 800;
			text-shadow: 0 4px 4px transparentize($black, .75); }
		p {
			font-size: 20px;
			font-weight: 800;
			text-shadow: 0 2px 4px transparentize($black, .75);
			margin-top: 20px;
			span {
				color: $green; } } }

	.linear-gradient {
		background: linear-gradient(to right, $green, $celeste); }

	.welcome {
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
		color: $white;
		margin-bottom: 100px;
		h1 {
			color: $blue;
			margin-bottom: 100px; }
		p {
			font-size: 34px;
			font-weight: 900;
			line-height: 40px;
			text-shadow: 0 4px 10px transparentize($black, .9), 0 2px 4px transparentize($black, .8);
			width: 50%; }
		span {
			color: $blue;
			font-size: 40px;
			text-shadow: 0 4px 10px transparentize($blue, .9), 0 2px 4px transparentize($blue, .8); } }

	.about {
		h2 {
			color: $blue;
			margin-bottom: 10px;
			align-self: flex-start; }
		.columns {
			.column {
				display: flex;
				flex-direction: column;
				align-items: center;
				width: 50%; }
			.column:nth-child(1) {
				p {
					font-size: 30px;
					font-weight: 900;
					line-height: 36px;
					opacity: .75;
					background: -webkit-linear-gradient(-90deg, #28A5EB, $blue);
					-webkit-background-clip: text;
					-webkit-text-fill-color: transparent;
					margin-bottom: 50px; } } }
		.card {
			background-color: $white;
			color: $black;
			text-align: left;
			width: 450px;
			border-radius: rem(10px);
			padding: rem(40px) rem(20px);
			transform: rotate(1.14deg);
			box-shadow: 0 0 rem(50px) transparentize($black, .9), 0 0 rem(15px) transparentize($black, .8), 0 0 rem(5px) transparentize($black, .9);
			.top {
				text-align: center;
				& > img {
					border-radius: 100%;
					height: 125px;
					width: 125px;
					box-shadow: 0 0 10px transparentize($black, .9);
					margin-bottom: rem(20px); }
				h4 {
					font-size: rem(24px);
					font-weight: normal;
					& ~ div {
						display: flex;
						justify-content: center;
						align-items: center;
						margin-top: rem(10px);
						margin-bottom: rem(20px);
						p {
							margin: 0 rem(5px);
							font-style: italic;
							width: fit-content; }
						img {
							height: rem(19px); } } } }
			.middle, .bottom {
				border-top: rem(1px) solid transparentize($black, .8);
				padding-top: rem(20px);
				h5 {
					font-size: rem(16px);
					font-weight: bold;
					margin-bottom: rem(20px); } }
			.middle {
				padding-bottom: rem(15px);
				p {
					float: left;
					margin: 0 rem(10px) rem(5px) 0;
					font-size: rem(16px);
					color: white;
					background-color: $blue;
					border-radius: rem(3px);
					padding: rem(5px);
					width: fit-content;
					line-height: 1.2; } }
			.bottom {
				div {
					display: flex;
					flex-direction: row;
					a {
						display: flex;
						height: 30px;
						width: 30px;
						border-radius: 100%;
						justify-content: center;
						align-items: center;
						margin-right: rem(10px);
						transition: all .2s;
						&.github {
							background-color: $github; }
						&.linkedin {
							background-color: $linkedin; }
						&.dribble {
							background-color: $dribble; }
						&.instagram {
							background-color: $instagram; }
						i {
							color: $white;
							font-size: rem(18px); }
						&:hover {
							opacity: .5; } } } } } }
	.services {
		h2 {
			color: $white;
			margin-top: 200px;
			margin-bottom: 100px;
			text-shadow: 0px 4px 4px transparentize($black, .75);
			span {
				color: $green; } }
		.container {
			padding: rem(50px) 0 rem(100px) 0;
			h3 {
				text-align: center;
				font-size: rem(40px);
				color: $black;
				margin-bottom: rem(50px); }
			.column {
				display: flex;
				text-align: center;
				flex-direction: column;
				align-items: center;
				padding: rem(20px);
				h4 {
					color: $black;
					font-weight: 700;
					margin-bottom: 10px; }
				p {
					color: $darkBlue;
					text-align: left; } } } }

	.projects {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: 250px;
		h2 {
			color: $white;
			margin-bottom: 50px;
			text-shadow: 0px 4px 4px transparentize($black, .75);
			span {
				color: $green; } }
		a {
			display: flex;
			align-items: center;
			background-color: $white;
			border-radius: 6px;
			padding: 18px 60px;
			box-shadow: 0 10px 15px transparentize($black, .8), 0 4px 4px transparentize($black, .75);
			img {
				height: 20px; }
			p {
				font-size: 20px;
				font-weight: 700;
				margin: 0 5px; } } }

	.action {
		h1 {
			margin: 0;
			padding: 0 10px;
			color: white;
			font-size: rem(96px);
			font-weight: 900;
			line-height: rem(115px);
			position: relative;
			z-index: 5;
			span {
				display: block;
				&:nth-child(1) {
					text-shadow: 0 rem(4px) rem(4px) transparentize($black, .75); }

				&:nth-child(2) {
					color: $homeBlue;
					text-shadow: 0 rem(4px) rem(4px) transparentize($black, .85); } } }
		img {
			display: block;
			width: 100%;
			margin-top: rem(-320px);
			transform: translateY(1px); }
		div {
			display: flex;
			justify-content: center;
			align-items: center;
			background-color: $white;
			height: 350px;
			a {
				// background-color: $homeBlue
				background-color: #187df7;
				color: $white;
				// text-shadow: 0 1px 2px transparentize($black, .8)
				border: 0;
				border-radius: rem(5px);
				font-size: rem(18px);
				font-weight: 700;
				padding: 15px 80px;
				box-shadow: 0 2px 2px transparentize($homeBlue, .8),0 5px 10px transparentize($homeBlue, .3);
				transition: all .2s;
				&:hover {
					background-color: #1063c7;
					box-shadow: 0 2px 2px transparentize(#1063c7, .8),0 5px 10px transparentize(#1063c7, .3); } } } }
	footer {
		color: $black;
		background-color: $white;
		.footer-top .footer-icons {
			a {
				background-color: $homeBlue;
				box-shadow: 0 0 0; }
			i {
				color: $white; } }
		.footer-divider {
			background-color: transparentize($grey, .8); }
		.footer-bottom a p {
			color: $darkBlue;
			&:hover {
				color: $black; } } } }
