// MOBILE L: FROM 576 TO 768
@media screen and (min-width: $mobile-l) and (max-width: $to-tablet) {
	.portfolio {
		main .columns .column {
			.categories p {
				font-size: rem(16px); } } } }

// TABLET: FROM 768 TO 1023
@media screen and (max-width: $to-desktop-s) {
	header .navbar .navbar-menu {
		.navbar-end {
			position: fixed;
			width: 50%;
			height: 100%;
			top: 0;
			right: -50%;
			text-align: center;
			transition: all .5s;
			button {
				display: block; }
			.background {
				position: fixed;
				left: 0;
				top: 0;
				z-index: -1;
				width: 100vw;
				height: 100vh;
				background-color: transparentize($black, .25);
				opacity: 0;
				transition: all .5s; }
			.closeMenu {
				position: absolute;
				display: flex;
				justify-content: center;
				align-items: center;
				flex-direction: column;
				z-index: 5;
				height: 20px;
				width: 20px;
				top: 20px;
				left: 30px;
				&:hover {
					cursor: pointer;
					span:nth-child(1) {
						transform: rotate(135deg); }
					span:nth-child(2) {
						transform: rotate(45deg); } }
				span {
					position: absolute;
					height: 3px;
					width: 100%;
					background-color: $black;
					transition: all .2s;
					&:nth-child(1) {
						transform: rotate(45deg); }
					&:nth-child(2) {
						transform: rotate(-45deg); } } }
			&.mobileMenuOpen {
				right: 0;
				.closeMenu {
					@include animation (closeButtonIn, 2s, 1, ease);
					@include animation-fill-mode (forwards); }
				.background {
					opacity: 1; } } }
		ul {
			position: fixed;
			top: 0;
			background-color: $white;
			height: 100%;
			width: inherit;
			margin: 0;
			flex-direction: column;
			justify-content: flex-end;
			align-items: flex-start;
			padding: rem(20px) rem(60px);
			font-size: rem(28px);
			box-shadow: rem(-4px) 0 rem(50px) transparentize($black, .75);
			transition: all .5s; }
		li {
			font-weight: normal;
			color: transparentize($black, .5);
			margin: 0;
			margin-bottom: rem(20px);
			&:last-child {
				margin-bottom: 0; }
			.current-page {
				color: $black;
				font-weight: 600;
				border-bottom: 0;
				&:after {
					content: '';
					position: absolute;
					left: rem(-40px);
					top: 50%;
					transform: translateY(-50%);
					background-color: $black;
					height: rem(2px);
					width: rem(20px); } }
			&:hover {
				color: $black; } } }
	.about {
		main {
			h1 ~ .columns {
				display: flex;
				flex-direction: column-reverse; }
			.card {
				margin-bottom: 50px; }
			.columns .column {
				display: flex;
				flex-direction: column;
				align-items: center;
				flex-basis: auto;
				p {
					text-align: justify;
					text-align-last: center; } } } }
	.contact {
		main {
			form {
				& * {
					width: 80%; } } }
		// .waves
		// 	.wave1
		// 		background-image: url("../src/contact/contact-tablet-wave1.svg")
		// 	.wave2
 }		// 		background-image: url("../src/contact/contact-tablet-wave2.svg")
	.portfolio {
		main {
			.columns .column .categories {
				height: calc(100vw / 100 * 80 / 2 - 10px); } }
		.modal {
			.presentation .content {
				display: none; } } } }

@media screen and (min-width: $tablet) {
	.no-desktop {
		display: none !important; }

	.home {
		.action {
			h1 {
				font-size: rem(80px); } } }
	.about {
		main {
			.columns .column {
				&:nth-child(2) .card {
					width: 80%; } }
			.action {
				a {
					width: auto; } } } }

	.portfolio {
		main {
			.filterButtons p:nth-child(2) {
				display: none; } } } }

@media screen and (min-width: $tablet) and (max-width: $to-desktop-s) {
	.home {
		.services {
			div:nth-child(2) .container .columns {
				flex-wrap: wrap;
				.column {
					flex: 0 50%;
					p {
						text-align: center; } } } } } }

// DESKTOP: FROM 1024 TO 1215
@media screen and (min-width: $desktop-s) {
	header {
		&.header-hover {
			transition: background-color .2s;
			&:hover {
				background-color: transparentize($black, .5) !important; } }
		.navbar .navbar-burger {
			display: none; } }
	.home {
		.showcase {
			.columns {
				h2.column.selected:after, h2:hover:after {
					width: 95%; } }
			.work-samples {
				.back-sample-1, .back-sample-2 {
					display: block; } } }
		.action {
			h1 {
				font-size: rem(96px);
				line-height: rem(115px); } } }
	.portfolio {
		main {
			.columns .column {
				.categories {
					height: calc(100vw / 100 * 80 / 3 - 10px); } } } }
